<template>
  <div class="withdraw">
    <el-form 
      class="withdraw__form" 
      ref="withdraw" 
      :model="withdraw"
      :rules="rules" 
      label-width="120px">
      <el-form-item label="提現金額 :" prop="amount">
        <el-input class="withdraw__input" size="mini" v-model="withdraw.amount">
        </el-input>
      </el-form-item>
      <el-form-item label="提现地址 :" prop="wallet_address_id">
        <el-select size="mini" v-model="withdraw.wallet_address_id" placeholder="请选择">
          <el-option
            v-for="item in withdrawList"
            :key="item.value"
            :label="item.label"
            :value="item.value">
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="支付密码 :" prop="pay_password">
        <el-input class="withdraw__input" size="mini" v-model="withdraw.pay_password"></el-input>
      </el-form-item>
      <span class="withdraw__p" @click="$router.push({ name: 'findWithdrawPassword' })">忘記支付密碼?</span>
      <el-form-item>
        <el-button size="mini" type="primary" @click="onSubmit">提現</el-button>
      </el-form-item>
    </el-form>    
  </div>
</template>

<script>
import { withdraw } from "@/api/wallet.js";
import { withdrawList } from "@/api/withdraw.js";
import { getUserInfo } from "@/api/common";
export default {
  name: "withdraw",
  data() {
    return {
      withdraw: {
        amount: "",
        wallet_address_id: "",
        pay_password: ""
      },
      rules: {
        amount: [
           { required: true, message: '请输入提现金额', trigger: 'blur' }
        ],
        wallet_address_id: [
           { required: true, message: '请输入钱包地址ID', trigger: 'blur' }
        ],
        pay_password: [
           { required: true, message: '请输入支付密码', trigger: 'blur' }
        ]
      },
      timerFlag: false,
      withdrawList: []
    }
  },
  watch: {
    "$route"(to) {
      if(to.name == 'withdraw') {
        this.getWithdrawList();
      }
    }
  },
  computed: {
    userBalance: {
      get() {
        return this.$store.state.user.balance;
      },
      set(val) {
        this.$store.commit("user/updateBalance", val);
      }
    }
  },
  methods: {
    getWithdrawList() {
      this.withdrawList = [];
      withdrawList().then(({ data }) => {
        data.map(i => {
          this.withdrawList.push({ value: i.id, label: i.name });
        })
      });
    },
    getUserInfo() {
      getUserInfo().then(({ data }) => {
        this.userBalance = data.cash_wallet.balance
      });
    },
    onSubmit() {
      this.$refs['withdraw'].validate((valid) => {
        if (valid) {
          withdraw(this.withdraw).then(() => {
            this.$message.success("提交成功, 等待审核!");
            this.getUserInfo();
            this.reset();
            this.$router.push({ name: 'wallet' });
          }).catch(data => {
            this.$message.error(data.response.data.message);
          })
        }
      });
    },
    reset() {
      this.$refs['withdraw'].resetFields();
    }
  },
  mounted() {
    this.getWithdrawList();
  }
}
</script>

<style lang="scss">
$prefix: withdraw;

.#{$prefix} {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 80vh;
  background: #EBEEF5;
  &__p {
    color: #00a1d6;
    margin: 0 0 0 120px;
    padding: 0;
    cursor: pointer;
  }
  &__form {
    width: 40%;
  }
  &__input {
    width: 350px;
  }
  &__code {
    color: #00a1d6;
    cursor: pointer;
  }
}
</style>