import http from "./http.js";

/**
 * 获取 `提现地址列表`
 *
 * @author yq <447845126@qq.com>
 * @time   2019-02-13
 */
export function withdrawList() {
  return http.get("/withdraw/address")
}

/**
 * 增加/修改 `提现地址`
 *
 * @author yq <447845126@qq.com>
 * @time   2019-02-13
 */
export function withdrawAddOrUpdate({id, ...data}) {
  let method = "POST";
  let url = "/withdraw/address";

  if(id > 0) {
    method = "PUT";
    url += `/${id}`;
    data.id = id; 
  }

  return http({ url, method, data });
}

/**
 * 删除 `提现地址`
 *
 * @author yq <447845126@qq.com>
 * @time   2019-02-13
 * @param  {[type]}   id [description]
 * @return {[type]}      [description]
 */
export function deleteWithdraw(id) {
  return http.delete(`/withdraw/address/${id}`);
}